	module.exports = {"data":{"markdownRemark":{"html":"<p>法学部発展戦略企画グループ会議では、3学科の参加者全員が意見を述べ、多くの良いアイディアを持っていました。重要なのは、アイデアを持たず、アイデアがなく、何もできないことです。基本的な戦略は、まず、ロースクールの内部開発の方向性、すなわち学術発展の方向性、攻撃の主要領域、重要なブレークスルー領域を決定し、このセンターのすべてのリソース、管理、教授および科学研究を整理することです。特に学生の育成のために、これは私たちの教育ユニットの製品であり、最終的には市場で競争しなければなりません。人々の競争はおそらく、すべての市場競争において最も強く不可欠な競争であろう。</p>\n<p>議論の結果は次のとおりです。</p>\n<p>法律学校全体の観点から、政策科学、国際文化（歴史と人文明の現状）、環境科学、行政科学、人権問題、社会開発など、共同で戦うことが可能です。</p>\n<p>さまざまな部門の視点から、ビジネスの人口統計、都市社会学、コミュニティ開発、社会的事業、民間商業法の比較、国際経済法、法思想の歴史などの発展方向が考えられます。</p>\n<p>さらに発展させるには、我々は考え続け、衝突し続けなければならない。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0164.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0164.md","frontmatter":{"title":"10月21日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0164.md absPath of file >>> MarkdownRemark"}}

